import * as tslib_1 from "tslib";
import { Component, Vue } from "vue-property-decorator";
import { GetTodayDailyStudyAsync, SendDailyStudyAsync } from "@/api/daily-study";
import { GetMyCoursesListAsync } from "@/api/course-group";
let CeteleDoldur = class CeteleDoldur extends Vue {
    constructor() {
        super(...arguments);
        this.courseList = [];
        this.dailyStudy = {};
        this.tableLoading = true;
    }
    mapCourseIdToCourse(id) {
        //  mapCourseIdToCourse(scope.row.id)
        if (this.courseList.length !== 0) {
            let found = this.courseList.find(c => {
                return c.id == id;
            });
            return found.name;
        }
    }
    ChangedAmount(row) {
        if (row.begining != row.end) {
            if (row.begining > row.end) {
                row.amount = row.end - row.begining;
            }
            else {
                row.amount = row.end - row.begining + 1;
            }
        }
        else {
            if (row.amount < 0)
                row.amount = 0;
        }
    }
    async SendCetele() {
        try {
            const { data, status } = await SendDailyStudyAsync(this.dailyStudy.id, this.dailyStudy.studies);
            console.log(status);
            if (status == 200) {
                this.$message({
                    message: 'Çeteleniz başarıyla kaydedildi.',
                    type: 'success',
                    duration: 2000,
                });
            }
            console.log(data);
        }
        catch (e) {
            console.log(e);
        }
    }
    async created() {
        //this.tableLoading = true;
        try {
            const { data: courses } = await GetMyCoursesListAsync();
            this.courseList = courses;
            const { data: dailyStudy } = await GetTodayDailyStudyAsync();
            this.dailyStudy = dailyStudy;
        }
        catch (e) {
            console.log(e);
        }
        finally {
            this.tableLoading = false;
        }
    }
};
CeteleDoldur = tslib_1.__decorate([
    Component
], CeteleDoldur);
export default CeteleDoldur;
